"use client";

import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const slides = [
  {
    id: 1,
    bgColor: "bg-indigo-900",
    title: "Creative Portfolio",
    subtitle: "Web Developer & UI/UX Designer",
    description: "Creating digital experiences that connect and inspire",
  },
  {
    id: 2,
    bgColor: "bg-purple-900",
    title: "Featured Projects",
    subtitle: "Innovative Solutions",
    description: "Discover my latest work and creative process",
  },
  {
    id: 3,
    bgColor: "bg-blue-900",
    title: "Technical Skills",
    subtitle: "Expert Technologies",
    description: "Frontend, backend, and everything in between",
  },
  {
    id: 4,
    bgColor: "bg-pink-900",
    title: "Let's Connect",
    subtitle: "Work Together",
    description: "Ready to bring your vision to life",
  },
];

const AnimatedSlider = () => {
  const containerRef = useRef(null);
  const slidesRef = useRef([]);

  useEffect(() => {
    if (!containerRef.current) return;

    const totalSlides = slides.length;

    // Set widths dynamically
    gsap.set(slidesRef.current, { width: "100vw", height: "100vh" });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: containerRef.current,
        pin: true,
        scrub: 1,
        end: () => `+=${window.innerHeight * (totalSlides - 1)}`,
      },
    });

    // Animate horizontal scroll
    tl.to(slidesRef.current, {
      xPercent: -100 * (totalSlides - 1),
      ease: "none",
    });

    // Animate content inside each slide
    slidesRef.current.forEach((slide, index) => {
      const content = slide.querySelector(".slide-content");
      const animatedElements = slide.querySelectorAll(".animated-element");

      gsap.fromTo(
        content,
        { opacity: 0, y: 50 },
        {
          opacity: 1,
          y: 0,
          duration: 0.6,
          scrollTrigger: {
            trigger: slide,
            containerAnimation: tl,
            start: "left center",
            toggleActions: "play none none reverse",
          },
        }
      );

      gsap.fromTo(
        animatedElements,
        { opacity: 0, y: 20 },
        {
          opacity: 1,
          y: 0,
          stagger: 0.1,
          duration: 0.4,
          scrollTrigger: {
            trigger: slide,
            containerAnimation: tl,
            start: "left center",
            toggleActions: "play none none reverse",
          },
        }
      );
    });

    return () => {
      ScrollTrigger.getAll().forEach((t) => t.kill());
    };
  }, []);

  return (
    <div
      ref={containerRef}
      className="relative w-screen h-screen overflow-hidden"
    >
      <div className="flex w-max h-screen">
        {slides.map((slide, index) => (
          <section
            key={slide.id}
            ref={(el) => (slidesRef.current[index] = el)}
            className={`flex-shrink-0 flex flex-col justify-center items-center ${slide.bgColor}`}
          >
            <div className="slide-content text-white max-w-4xl px-8 opacity-0">
              <h2 className="text-6xl font-bold mb-4">{slide.title}</h2>
              <h3 className="text-3xl mb-6">{slide.subtitle}</h3>
              <p className="text-xl mb-8">{slide.description}</p>

              {index === 0 && (
                <div className="flex space-x-4">
                  <button className="animated-element bg-white text-indigo-900 px-6 py-2 rounded-full font-bold transition hover:bg-opacity-80">
                    View Portfolio
                  </button>
                  <button className="animated-element border-2 border-white text-white px-6 py-2 rounded-full font-bold transition hover:bg-white hover:bg-opacity-20">
                    Contact Me
                  </button>
                </div>
              )}
            </div>
          </section>
        ))}
      </div>
    </div>
  );
};

export default AnimatedSlider;
